import { Component, Prop, h, Host } from '@stencil/core';

@Component({
  tag: 'ui-notification',
  styleUrl: 'ui-notification.css',
  shadow: true,
})
export class UiNotification {
  @Prop() type: 'success' | 'error' | 'info' | 'warning' = 'info';
  @Prop() placement: 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right' = 'top-right';
  @Prop() header: string = '';
  @Prop() content: string = '';

  render() {
    return (
      <>
        <ix-style-loader></ix-style-loader>
        <Host class={`notification ${this.placement} ${this.type}`}>
          <div class="notification-box">
            {this.header && <div class="header">{this.header}</div>}
            <div class="content">{this.content}</div>
          </div>
        </Host>
      </>

    );
  }
}
